export const add = (num) => {
    return{
        type: 'ADD',
        num: num
    }
}

export const minus = (num) => {
    return{
        type: 'MINUS',
        num: num
    }
}

export const addStepNum = (step) => {
    return{
        type: 'ADDSTEPNUM',
        step: step
    }
}

export const minusStepNum = (step) => {
    return{
        type: 'MINUSSTEPNUM',
        step: step
    }
}