import React from 'react';
import styled from 'styled-components';
import {Switch, Route} from 'react-router-dom';

import Steps from '../../stories/atoms/Steps';
import Info from '../../stories/organisms/Info';
import Form from './Form';
import LinePay from '../Deposit/PaymentMethod/CustomPayment/LinePay';
import Atm from '../Deposit/PaymentMethod/CustomPayment/Atm';
import Card from '../Deposit/PaymentMethod/CustomPayment/Card';
import Store from '../Deposit/PaymentMethod/CustomPayment/Store';
import UnionPay from '../Deposit/PaymentMethod/CustomPayment/UnionPay';
import Pending from '../Deposit/Pending'

const stepName = ['確認購物車', '選擇配送付款方式', '確認訂購']

type Props = {};

type State = {
    activeStep: number,
    CartArray: array
};

class Home extends React.PureComponent<Props, State> {
    state = {
        activeStep: 1
    }

    handleStep = (value) => {
        const {activeStep} = this.state

        this.setState({
            activeStep: activeStep + 1
        })
    }

    render() {
        const {CartArray, step} = this.props
        const {activeStep} = this.state

        return(
            <>
                <StepsRoot activeStep={activeStep} stepName={stepName}/>
                <Info/>
                <Switch>
                    <Route path="/" exact component={Form}>
                        <Form onClick={this.handleStep} CartArray={CartArray}/>
                    </Route>
                    <Route path={`/Store`} component={Store} />
                    <Route path={`/Card`} component={Card} />
                    <Route path={`/LinePay`} component={LinePay} />
                    <Route path={`/UnionPay`} component={UnionPay} />
                    <Route path={`/Atm`} component={Atm} />
                    
                    <Route path={`/Pending`} component={Pending} />
                </Switch>
            </>
        )
    }
};

export default Home;

const StepsRoot = styled(Steps)`
    margin-bottom: -20px;
    position: relative;
`;